import React, { Component } from "react";
import axios from "axios";

export default class Index extends Component {
  constructor(props) {
    super(props);
    this.state = { movies: {}, loading: false, value: "" };
    this.queryForMovies = this.queryForMovies.bind(this);
    const apiKey = "e3963faaff14ce2935d481ee7d0179fb";
    this.url = `https://api.themoviedb.org/3/search/movie?query=${this.state.value}&api_key=${apiKey}`;
  }

  async queryForMovies(queryText) {
    this.setState({ value: queryText });
    this.setState({ loading: true });
    const response = await axios(this.url);
    const movies = await response.data.results;
    console.log(movies);
  }
  render() {
    return (
      <div>
        <input
          type="text"
          placeholder="Search for..."
          value={this.state.value}
          onChange={event => this.queryForMovies(event.target.value)}
        />
      </div>
    );
  }
}
